import React from 'react';
import { Link } from 'react-router-dom';
import logo from '../assets/images/logos/logo.png';

const Home = () => {
    return (
        <div>
            <div className="banner-area text-center">
                <div>
                    <div className ="col-lg-12 middle-background" >
                    <div className="container-fluid">
                                <div className="row">
                                    <div className="col-md-6 align-self-center d-flex justify-content-center">
                                    <img className="w-100" src={logo} alt="Welcome logo"></img>
                                    </div>
                                    <div className="col-md-6 align-self-center d-flex justify-content-center">
                                        <div className="welcome-text mt-5 mt-md-0">
                                        <h3 className="style-change font-weight-bold">Welcome <br/>to Patidar foods</h3>
                                            <p className="white-text pt-3">Patidar Food is an authentic Indo Gujarati dining restaurant. It offers a wide variety of delicious and purely vegetarian cuisines. We bring our Indian and Gujarati culture alive. The authentic Gujarati and Indian food is served in Kitchener with love and respect. That will give you an extraordinary experience with true flavors of home-cooked food. Patidar food is the best option for Gujarati and Indian food. Our popular dishes include Chaat, Gujarati specials and Sweets, and myriad other cuisines that can be loved by your taste buds. We provide Catering and Take out orders and Tiffin service as well.</p>
                                            <Link to="/menu" className="template-btn mt-3">Check our menu here</Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Home
