import React from 'react';
import logo from '../assets/images/logos/logo.png';
import { Link } from 'react-router-dom';
import { Navbar, Nav } from 'react-bootstrap';

const Header = () => {

    return (
        <div>
             <header className="header-area">
                    <Navbar className='nav-background' collapseOnSelect fixed='top' expand='sm' variant='dark'>
                            <Link className="navbar-brand" to="/"><img  src={logo} alt="logo"></img></Link>
                            <Navbar.Toggle aria-controls='responsive-navbar-nav' />
                                <Navbar.Collapse id='responsive-navbar-nav'>
                                <Nav className="navlink ml-auto">
                                            <Link className = "nav-link" to="/">HOME</Link>
                                            <Link className = "nav-link" to="/menu">MENU</Link>
                                            <Link className = "nav-link" to="/catering">CATERING</Link>
                                            <Link className = "nav-link" to="/gallery">GALLERY</Link>
                                            <Link className = "nav-link" to="/contact">CONTACT</Link>
                                            <Link className = "nav-link" className="btn-lg btn-warning font-weight-bold" style={{color:'black'}} to="/orderNow">ORDER NOW</Link>
                                </Nav>
                                </Navbar.Collapse>
                    </Navbar>
            </header>
        </div>
    )
}

export default Header
