import React from 'react';
import { useState,useEffect } from 'react';
import thali1 from '../assets/images/MenuItems/Thali.jpg';
import thali2 from '../assets/images/MenuItems/Thali2.jpg';
import { Dropdown } from 'react-bootstrap';

const Catering = () => {

    const [displayDeluxThali, setDisplayDeluxThali] = useState(false);
    const [selectedChoice,setSelectedChoice] = useState(1);
    const [selectedChoicelabel,setSelectedChoicelabel] = useState('Classic Thali') 
    const [isMobile, setIsMobile] = useState(window.innerWidth <= 1205)
    const [showImage, setShowImage] = useState(window.innerWidth <= 765)

    const handleonclick = (i) =>{
        if ( i == 1) 
        { 
            setDisplayDeluxThali(false);
            setSelectedChoicelabel('Classic Thali');
        }
        else 
        { 
            setDisplayDeluxThali(true);
            setSelectedChoicelabel('Delux Thali');
        }
        setSelectedChoice(i);
    } 

    useEffect(() => {
        function handleResize() {
            console.log(window.innerWidth)
            if (window.innerWidth <= 1205){
                setIsMobile(true);
            }else{
                setIsMobile(false);
            }

            if (window.innerWidth <= 765){
                setShowImage(true);
            }else{
                setShowImage(false);
            }
    }
        window.addEventListener('resize', handleResize)
    })
    

    
    return (
        <div>
        <div className="banner-area text-center">
            
            <div>
                <div className ="col-lg-12 middle-background" >
                    <div className="container card d-block menu-outer" >
                        <div className="container rounded">
                            <div>
                            {isMobile ?
                            (
                                <Dropdown>
                                <Dropdown.Toggle className='m-2 btn-block' variant="warning" id="dropdown-basic">{selectedChoicelabel}</Dropdown.Toggle>
                                <Dropdown.Menu className='m-2 btn-block'>
                                    <Dropdown.Item onClick={() => handleonclick(1)}>Classic Thali</Dropdown.Item>
                                    <Dropdown.Item onClick={() => handleonclick(2)}>Delux Thali</Dropdown.Item>
                                </Dropdown.Menu>
                                </Dropdown>
                            ) :
                            (
                                <ul className="navbar justify-content-center">
                                    <li className="btn-lg btn-orange-color m-1" style={{backgroundColor: selectedChoice===1?"yellow":""}} onClick={() => handleonclick(1)}>Classic Thali</li>
                                    <li className="btn-lg btn-orange-color m-1" style={{backgroundColor: selectedChoice===2?"yellow":""}} onClick={() => handleonclick(2)}>Delux Thali</li>
                                </ul>
                            )
}

                            </div>

                        <div className="container-fluid">
                                <div className="row">
                                {!showImage ?
                                (<div className="col-md-6 card align-self-center d-flex justify-content-center m-0 menu-inner" >
                                    
                                        {displayDeluxThali 
                                        ? <img className="menu-inner" src={thali2} alt="Thali1"></img> 
                                        : <img className="menu-inner" src={thali1} alt="Thali2"></img>  
                                        }
                                        </div>)
                                        : ''}
  
                                    <div className="col-md-6 align-self-center d-flex justify-content-center">
                                        <div className="welcome-text mt-5 mt-md-0">
                                        { 
                                        displayDeluxThali 
                                        ? 
                                            <div>
                                                <h1>Delux Thali</h1>
                                                <h2>$9.99 </h2>
                                                <h4>(2 Curries, Dal/Kadhi, Rice, 5 Roti,Sweet)</h4> 
                                            </div>
                                        : 
                                            <div>
                                                <h1>Classic Thali</h1>
                                                <h2>$6.99</h2>
                                                <h4>(1 Curries, Dal/Kadhi, Rice, 3 Roti)</h4>  
                                            </div>
                                         }
                                        </div>
                                    </div>
                                </div>
                            </div>
                            </div>
                </div>
                </div>
                </div>
                </div>
                </div>

    )
}

export default Catering
