import React from 'react'
import { Link } from 'react-router-dom';
import logo from '../assets/images/logos/logo.png';

const pageNotFound = () => {
    return (
        <div>
            <div className="banner-area text-center">
                <div>
                    <div className ="col-lg-12 middle-background" >
                    <div className="container-fluid">
                                <div className="row">
                                    <div className="col-md-6 align-self-center d-flex justify-content-center">
                                        <img src={logo}></img>
                                    </div>
                                    <div className="col-md-6 align-self-center d-flex justify-content-center">
                                        <div className="welcome-text mt-5 mt-md-0">
                                        <h1>Opps! <br/>Page not found</h1>
                                            <Link to="/" className="template-btn mt-3">Go to home</Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default pageNotFound
