import React from 'react';
import { useState } from 'react';
import dahipuri from '../assets/images/dahi puri1.jpg';
import drink from '../assets/images/MenuItems/Drink.jpg';
import kachori from '../assets/images/MenuItems/KachoriChaat.jpg';
import khaman from '../assets/images/MenuItems/Khaman.jpg';
import naan from '../assets/images/MenuItems/Naan.jpg';
import pavbhaji from '../assets/images/PavBhaji.jpg';
import samosa from '../assets/images/MenuItems/Samosa.jpg';
import sandwich from '../assets/images/MenuItems/Sandwich.jpg';
import sweet from '../assets/images/MenuItems/Sweet.jpg';
import thali from '../assets/images/MenuItems/Thali.jpg';
import Carousel from 'react-bootstrap/carousel';

const ImageCarousel = () => {

    const [imageArray,setImageArray] = useState(
        [
            samosa,
            pavbhaji,
            dahipuri,
            kachori,
            khaman,
            sandwich,
            naan,
            thali,
            sweet,
            drink
        ]
    )

    return (
        <div className="container card carosel-inner">
                <div className="m-1">
                    <Carousel 
                    nextIcon = {<span aria-hidden="true" className="carousel-control-next-icon" />}
                    nextLabel = ""
                    prevIcon = {<span aria-hidden="true" className="carousel-control-prev-icon" />}
                    prevLabel = ""
                    indicators={false}
                    className="m-1"
                    >
                        {imageArray.map((image)=>{
                            return(
                                <Carousel.Item className="carosel-image">
                                    <img className="d-block w-100 rounded carosel-image" alt="FoodGalaey" src={image} />
                                </Carousel.Item>
                            )
                        })}
                    </Carousel>
                </div>
        </div>
    )
}

export default ImageCarousel
