import React from 'react';
import { useState,useEffect } from 'react';
import menu from '../assets/data/menuItems.json';
import type from '../assets/data/types.json';
import drink from '../assets/images/MenuItems/Drink.jpg';
import kachori from '../assets/images/MenuItems/KachoriChaat.jpg';
import khaman from '../assets/images/MenuItems/Khaman.jpg';
import naan from '../assets/images/MenuItems/Naan.jpg';
import pavbhaji from '../assets/images/PavBhaji.jpg';
import samosa from '../assets/images/MenuItems/Samosa.jpg';
import sandwich from '../assets/images/MenuItems/Sandwich.jpg';
import sweet from '../assets/images/MenuItems/Sweet.jpg';
import thali from '../assets/images/MenuItems/Thali.jpg';
import { Dropdown } from 'react-bootstrap';

//const  path = '..\assets\images\'; 

const Menu = () => {
    
    const [selectedMenu, setSelectedMenu] = useState(menu.filter((item)=>item.type === type[0].type));
    const [imageArray,setImageArray] = useState(
                                        [
                                            samosa,
                                            pavbhaji,
                                            kachori,
                                            khaman,
                                            sandwich,
                                            naan,
                                            thali,
                                            sweet,
                                            drink
                                        ]
                                    )
    const [selectedChoice, setSelectedChoice] = useState(0)
    const [selectedChoicelabel,setSelectedChoicelabel] = useState('Appetizers')
    const [isMobile, setIsMobile] = useState(window.innerWidth <= 1205)
    const [showImage, setShowImage] = useState(window.innerWidth <= 765)

    const handleonclick = (i) =>{
        let choice = type[i-1];
        setSelectedChoice(i-1);
        setSelectedChoicelabel(choice.displayName)
        setSelectedMenu(menu.filter((item)=>item.type === choice.type));
    } 

    useEffect(() => {
        function handleResize() {
            console.log(window.innerWidth)
            if (window.innerWidth <= 1205){
                setIsMobile(true);
            }else{
                setIsMobile(false);
            }

            if (window.innerWidth <= 765){
                setShowImage(true);
            }else{
                setShowImage(false);
            }
    }
        window.addEventListener('resize', handleResize)
    })
    
    return (
        <div>
        <div className="banner-area text-center">
            <div>
                <div className ="col-lg-12 middle-background" >
                    <div className="container card d-block menu-outer" >
                        <div className="container rounded">
                            {isMobile ?
                            (
                            <Dropdown>
                            <Dropdown.Toggle className='m-2 btn-block' variant="warning" id="dropdown-basic">{selectedChoicelabel}</Dropdown.Toggle>
                            <Dropdown.Menu className='m-2 btn-block'>
                                <Dropdown.Item onClick={() => handleonclick(1)}>Appetizer</Dropdown.Item>
                                <Dropdown.Item onClick={() => handleonclick(2)}>Entrees</Dropdown.Item>
                                <Dropdown.Item onClick={() => handleonclick(3)}>Chaat</Dropdown.Item>
                                <Dropdown.Item onClick={() => handleonclick(4)}>Gujarati Special</Dropdown.Item>
                                <Dropdown.Item onClick={() => handleonclick(5)}>Sandwiches</Dropdown.Item>
                                <Dropdown.Item onClick={() => handleonclick(6)}>Side Orders</Dropdown.Item>
                                <Dropdown.Item onClick={() => handleonclick(7)}>Thali</Dropdown.Item>
                                <Dropdown.Item onClick={() => handleonclick(8)}>Sweets</Dropdown.Item>
                                <Dropdown.Item onClick={() => handleonclick(9)}>Drinks</Dropdown.Item>
                            </Dropdown.Menu>
                            </Dropdown>
                            )
                            :
                            (<ul className = "navbar">
                                <li className="btn-lg btn-orange-color" style={{backgroundColor: selectedChoice===0?"yellow":""}} onClick={() => handleonclick(1)}>Appetizer</li>
                                <li className="btn-lg btn-orange-color" style={{backgroundColor: selectedChoice===1?"yellow":""}} onClick={() => handleonclick(2)}>Entrees</li>
                                <li className="btn-lg btn-orange-color" style={{backgroundColor: selectedChoice===2?"yellow":""}} onClick={() => handleonclick(3)}>Chaat</li>
                                <li className="btn-lg btn-orange-color" style={{backgroundColor: selectedChoice===3?"yellow":""}} onClick={() => handleonclick(4)}>Gujarati Special</li>
                                <li className="btn-lg btn-orange-color" style={{backgroundColor: selectedChoice===4?"yellow":""}} onClick={() => handleonclick(5)}>Sandwiches</li>
                                <li className="btn-lg btn-orange-color" style={{backgroundColor: selectedChoice===5?"yellow":""}} onClick={() => handleonclick(6)}>Side Orders</li>
                                <li className="btn-lg btn-orange-color" style={{backgroundColor: selectedChoice===6?"yellow":""}} onClick={() => handleonclick(7)}>Thali</li>
                                <li className="btn-lg btn-orange-color" style={{backgroundColor: selectedChoice===7?"yellow":""}} onClick={() => handleonclick(8)}>Sweets</li>
                                <li className="btn-lg btn-orange-color" style={{backgroundColor: selectedChoice===8?"yellow":""}} onClick={() => handleonclick(9)}>Drinks</li>
                            </ul>)
                        }
                        <div className="container-fluid">
                            <div className="row">
                            {!showImage ?
                            (
                                <div className="col-md-6 card align-self-center d-flex justify-content-center m-0 menu-inner" >
                                    <img className = "menu-inner" src={imageArray[selectedChoice]} alt="MenuItem"></img> 
                                </div>
                            ):''}
                                <div className="col-md-6 m-0"> 
                                    <div className="d-block">
                                        <div className="row"> 
                                                <div className="col-6"  >
                                                    <h4 style={{textAlign:'left'}} className="font-weight-bold">Items</h4>
                                                </div>
                                                <div className="col-6" >
                                                    <h4 style={{textAlign:'left'}} className="font-weight-bold">Price</h4>
                                                </div>
                                            </div>
                                        <div className="card py-2 menu-inner" >
                                            { selectedMenu.map((item) =>{
                                                return ( 
                                                    <div className="row">
                                                        <div className="col-6" >
                                                            <h4 style={{textAlign:'left'}}>{item.name}</h4>
                                                        </div>
                                                        <div className="col-6">
                                                            <h4 style={{textAlign:'left'}} className="font-weight-bold">{item.price}</h4>
                                                        </div>
                                                    </div>
                                                )
                                            })}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        </div>
                    </div>
                            
                </div>
            </div>
            </div>
        </div>
    )
}

export default Menu
