import React from 'react';
import skiplogo from '../assets/images/logos/skip.png';
import doordashlogo from '../assets/images/logos/doordash.jpg';
import ubereatslogo from '../assets/images/logos/uber.png';

const OrderNow = () => {
    return (
        <div>
            <div className="banner-area text-center">
                <div>
                    <div className ="col-lg-12 middle-background" >
                    <div className="container-fluid">
                                <div className="row">
                                    <div className="col-md-4 align-self-center d-flex justify-content-center">
                                        <a href='https://www.skipthedishes.com/'><img className="w-100 card logo-image" src={skiplogo} alt="skip the dish logo"></img></a>
                                    </div>
                                    <div className="col-md-4 align-self-center d-flex justify-content-center">
                                        <a href='https://www.doordash.com/'><img className="w-100 card logo-image" src={doordashlogo} alt="door dash logo"></img></a>
                                    </div>
                                    <div className="col-md-4 align-self-center d-flex justify-content-center">
                                        <a href='https://www.ubereats.com/ca'><img className="w-100 card logo-image" src={ubereatslogo} alt="uber eats logo"></img></a>
                                    </div>
                                </div>
                            </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default OrderNow
